import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "../Style/Login.css";
import { ApiHost } from "../../API";
import CloseEyeIcon from "../../assets/Icons/CloseEyeIcon";
import OpenEyeIcon from "../../assets/Icons/OpenEyeIcon";
import UserLoginIcon from "../../assets/Icons/UserLoginIcon";
import PasswordIcon from "../../assets/Icons/PasswordIcon";

const Login = () => {
  const [formData, setFormData] = useState({ user_name: "", password: "" });
  const [showPassword, setShowPassword] = useState(false);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  // ✅ Check token when the page loads
  useEffect(() => {
    const token = localStorage.getItem("shlen_token");
    if (token) {
      navigate("/dashboard"); // Redirect user if already logged in
    }
  }, [navigate]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setLoading(true);

    try {
      const response = await axios.post(`${ApiHost}/api/Users/login`, formData);
      const { token, userId, userName, role } = response.data;

      // ✅ Save data to localStorage with "shlen_" prefix
      localStorage.setItem("shlen_token", token);
      localStorage.setItem("shlen_user_id", userId);
      localStorage.setItem("shlen_user_name", userName);
      localStorage.setItem("shlen_role", role); // Store role if needed


      setFormData({ user_name: "", password: "" });
      navigate("/dashboard"); // ✅ Redirect after successful login
    } catch (err: any) {
      setError(err.response?.data?.message || "كلمه السر او المستخدم خطا!");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="login-page">
      <form className="login-form" onSubmit={handleLogin}>
        <div className="input-group">
          <label htmlFor="user_name">اسم المستخدم</label>
          <input
            type="text"
            name="user_name"
            placeholder="محمد علي محمد"
            value={formData.user_name}
            onChange={handleChange}
            required
          />
          <div className="icon">
            <UserLoginIcon />
          </div>
        </div>

        <div className="input-group">
          <label htmlFor="password">كلمة السر</label>
          <input
            type={showPassword ? "text" : "password"}
            name="password"
            placeholder="ادخل كلمة السر هنا ..."
            value={formData.password}
            onChange={handleChange}
            required
          />
          <div className="icon">
            <PasswordIcon />
          </div>
          <div className="eye-btn" onClick={() => setShowPassword(!showPassword)}>
            {showPassword ? <CloseEyeIcon/> : <OpenEyeIcon/>}
          </div>
        </div>

        {error && <p className="error">{error}</p>}

        <button type="submit" disabled={loading}>
          {loading ? "جاري تسجيل الدخول..." : "تسجيل الدخول"}
        </button>
      </form>
    </div>
  );
};

export default Login;
